body {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f4f4f4;
  background: linear-gradient(135deg, #00c6ff 0%, #0072ff 100%); /* Base gradient background */

}

.container {
  text-align: center;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.274);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  z-index: 2;
  position: relative;
}

input {
  padding: 10px;
  width: 90%;
  margin-bottom: 10px;
  font-size: 16px;
  border: 2px solid rgb(0, 0, 0);
  border-radius: 0.3rem;
}

button {
  padding: 10px;
  width: 45%;
  font-size: 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 0.3rem;  
  margin: 1rem 0.5rem 0.5rem 0.5rem;
  z-index: 999;
}

button:hover {
  background-color: #45a049;
}

.btn{
  display: inline;
}

#clearBtn{
  background-color: rgb(252, 41, 41);
}

#qrcode {
  margin-top: 20px;
}

#copiedMessage {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  z-index: 999;
}

.Msg {
  background-color: #45a049;
  padding: 10px 20px;
  color: white;
  border-radius: 0.3rem;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.modal-content button {
  margin: 10px;
  padding: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;
}

.modal-content button:hover {
  background-color: #45a049;
}

h1 {
  font-size: 3rem;
  text-transform: uppercase;
  background: linear-gradient(90deg, #000000, #323249);
  -webkit-background-clip: text;
  animation: glow 1.5s ease-in-out infinite alternate;
  text-shadow: 0px 4px 20px rgba(255, 255, 255, 0.8);
  color: #ffffffc0;
}


@media only screen and (max-width: 768px) {
  body {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f4f4f4;
    overflow-x: hidden; /* Prevent horizontal scroll */
    background: linear-gradient(135deg, #00c6ff 0%, #0072ff 100%);
  }

  .container {
      padding: 10px;
      margin-top: -10rem;
      margin: -12rem 1.5rem 2rem 1.5rem;
  }

  button {
      width: 80%;
      font-size: 14px;
  }

  #copiedMessage {
      top: 40%;
      left: 50%;
      padding: 5px 10px;
      font-size: 14px;
  }

  .btn{
    display: inline;
  }

  .btn button{
      width: 45%;
      margin: 20px 2.5% 0% 2.5%;
  }

  #clearBtn{
    margin-bottom: 1rem;
  }

  .modal-content{
    background-color: #ffffff;
    margin: 3rem;
  }

  .clear button{
    width: fit-content;
  }

  .clear{
    margin-top: 0.5rem;
  }

  section {
    position: absolute;
    top: 0;
    left: 0; 
    width: fit-content;
    height: 100vh;
    background: url("https://i.postimg.cc/c1Q3njM0/bg2.jpg") no-repeat center center fixed;
    background-size: cover;
    animation: animateBg 50s linear infinite;
    overflow-x: hidden; /* Prevent horizontal scroll */
  }


  
}

section {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: url("https://i.postimg.cc/c1Q3njM0/bg2.jpg") no-repeat center center fixed;
  background-size: cover;
  animation: animateBg 50s linear infinite;
}

@keyframes animateBg{
  0%,100%{
      transform: scale(1);
  }
  50%{
      transform: scale(1.2);
  }
}

span{
  position: absolute;
  top:50%;
  left:50%;
  width: 4px;
  height: 4px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 0 0 4px rgba(255,255,255,0.1),0 0 0 8px rgba(255,255,255,0.1),0 0 20px rgba(255,255,255,0.1);
  animation: animate 3s linear infinite;
}
span::before{
  content:'';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 300px;
  height: 1px;
  background: linear-gradient(90deg,#fff,transparent);
}
@keyframes animate
{
  0%
  {
      transform: rotate(315deg) translateX(0);
      opacity: 1;
  }
  70%
  {
      opacity: 1;
  }
  100%
  {
      transform: rotate(315deg) translateX(-1000px);
      opacity: 0;
  }
}
span:nth-child(1){
  top: 0;
  right: 0;
  left: initial;
  animation-delay: 0s;
  animation-duration: 1s;
}
span:nth-child(2){
  top: 0;
  right: 80px;
  left: initial;
  animation-delay: 0.2s;
  animation-duration: 3s;
}
span:nth-child(3){
  top: 80;
  right: 0px;
  left: initial;
  animation-delay: 0.4s;
  animation-duration: 2s;
}
span:nth-child(4){
  top: 0;
  right: 180px;
  left: initial;
  animation-delay: 0.6s;
  animation-duration: 1.5s;
}
span:nth-child(5){
  top: 0;
